.text-null a{
    text-decoration: none;
}
.txt-center {
    display: flex;
    margin: auto;
    font-family: 'Noto Serif Khmer', serif;
    font-weight: bold;
    color: black;
    padding: 20px 30px;
    box-shadow: 0px 0px 10px 10px rgba(47, 91, 234, 0.175);
    border: 2px solid rgba(47, 91, 234, 0.175);
    border-radius: 50px;
    background-color: white;
    cursor: pointer;
}

.txt-center:hover {
    color: white;
    background-color:rgb(30, 37, 48);
}

.row {
    display: -ms-flexbox;
    /* IE10 */
    display: flex;
    -ms-flex-wrap: wrap;
    /* IE10 */
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 4px;
}

/* Create four equal columns that sits next to each other */
.column {
    -ms-flex: 25%;
    /* IE10 */
    flex: 25%;
    max-width: 25%;
    padding: 0 4px;
}

.column img {
    margin-top: 8px;
    vertical-align: middle;
    width: 100%;
}

/* Responsive layout - makes a two column-layout instead of four columns */



/* The hover effect
-------------------------------------------------------------- */   
    .column img:hover {
    opacity: .35;
    transition: all .5s ease;
    transform: scale(1);
    cursor: crosshair;
    /* cursor: help; */
    /* cursor: not-allowed; */
    /* cursor: zoom-in; */
    /* cursor: zoom-out; */
    /* cursor: wait; */
    }
@media screen and (max-width: 100px) {
    .column {
        -ms-flex: 33.33%;
        flex: 33.33%;
        max-width: 33.33%;
    }
}

@media screen and (max-width: 800px) {
    .column {
        -ms-flex: 33.33%;
        flex: 33.33%;
        max-width: 33.33%;
    }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
    .column {
        -ms-flex: 100%;
        flex: 100%;
        max-width: 100%;
    }
}