@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Khmer&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Noto Serif Khmer', serif;
}
nav {
  display: flex;
  justify-content: center;
 
}

nav ul {
  display: flex;
  list-style-type: none;
  gap: 10px;
}
nav ul a{
  text-decoration: none;
}

nav ul li {
  font-size: 1.3rem;
  font-weight: bold;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 10px;
}

h1 {
  margin: 0;
  font-size: 2rem;
}

p {
  font-size: 1.4rem;
  font-weight: medium;
  color: rgb(111, 111, 111);
}

main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.buttons {
  font-family: 'Noto Serif Khmer', serif;
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.cta {
  font-family: 'Noto Serif Khmer', serif;
  font-weight: bold;
  padding: 10px 20px;
  cursor: pointer;
}

.hire {
  background-color: #fff;
  border: 2px solid black;
}

.hire:hover {
  background-color: black;
  color: white;
}

.project {
  background-color: black;
  border: 2px solid black;
  color: white;
}

.project:hover {
  background-color: white;
  color: black;
}

figure {
  margin: 0;
  position: relative;
}

.me-img {
  border: 5px solid black;
  border-bottom-left-radius: 50%;
  cursor: grab;
}

.img-bg {
  width: 350px;
  height: 450px;
  /* background-color: black; */
  position: absolute;
  top: -10px;
  left: 20px;
  border-bottom-left-radius: 50%;
  z-index: -1;
}

@media screen and (min-width:800px) {

  .content {
    flex-direction: row;
    /* margin: 80px; */
  }

  h1 {
    font-size: 3.5rem;
  }

  .cta { 
    padding: 20px 30px;
    font-size: 1.2rem;
  }

  .buttons {
    margin: 0; 
  }
  .fx{
    display: flex;
    justify-content: space-between;
  }
  
nav {
  display: flex;
  justify-content: flex-end;
  padding: 0 30px;
}

nav ul {
  display: flex;
  list-style-type: none;
  gap: 5px;
}
nav ul a{
  text-decoration: none;
}

nav ul li {
  font-size: 1.3rem;
  font-weight: bold;
}
 
}